import React, { useState, useEffect } from "react";
import "./ChurchHistoryTimeline.css";

const ChurchHistoryTimeline = ({ text, language }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [isAutoPlay, setIsAutoPlay] = useState(true);

  // Parse the text into timeline sections
  const parseTimelineSections = () => {
    let sections = [];

    if (language === "amharic" || language === "am") {
      // Amharic sections
      const amharicSections = [
        {
          title: "  ",
          titleEnglish: "Pre Church",
          year: "2001",
          content: text.substring(
            text.indexOf("  "),
            text.indexOf(" ")
          ),
        },
        {
          title: " ",
          titleEnglish: "Establishment of the Church",
          year: "2018",
          content: text.substring(
            text.indexOf(" "),
            text.indexOf(" ")
          ),
        },
        {
          title: " ",
          titleEnglish: "Future Plan",
          year: "2023",
          content: text.substring(text.indexOf(" ")),
        },
      ];
      sections = amharicSections;
    } else {
      // English sections
      const englishSections = [
        {
          title: "Pre church",
          titleEnglish: "Pre Church",
          year: "2001",
          content: text.substring(
            text.indexOf("Pre church"),
            text.indexOf("Establishment of the church")
          ),
        },
        {
          title: "Establishment of the church",
          titleEnglish: "Establishment of the Church",
          year: "2018",
          content: text.substring(
            text.indexOf("Establishment of the church"),
            text.indexOf("Future plan")
          ),
        },
        {
          title: "Future plan",
          titleEnglish: "Future Plan",
          year: "2023",
          content: text.substring(text.indexOf("Future plan")),
        },
      ];
      sections = englishSections;
    }

    return sections.filter((s) => s.content.trim().length > 0);
  };

  const sections = parseTimelineSections();

  // Auto-play functionality
  useEffect(() => {
    if (!isAutoPlay) return;

    const interval = setInterval(() => {
      setActiveIndex((prev) => (prev + 1) % sections.length);
    }, 7200); // 7.2 seconds - accounts for 0.7s animations + reading time

    return () => clearInterval(interval);
  }, [isAutoPlay, sections.length]);

  const handleSectionClick = (index) => {
    setActiveIndex(index);
    setIsAutoPlay(false);
  };

  const handleNext = () => {
    setActiveIndex((prev) => (prev + 1) % sections.length);
    setIsAutoPlay(false);
  };

  const handlePrev = () => {
    setActiveIndex((prev) => (prev - 1 + sections.length) % sections.length);
    setIsAutoPlay(false);
  };

  const toggleAutoPlay = () => {
    setIsAutoPlay(!isAutoPlay);
  };

  return (
    <div className="church-history-timeline">
      <div className="timeline-header">
        <button
          className="autoplay-toggle"
          onClick={toggleAutoPlay}
          title={isAutoPlay ? "Pause" : "Play"}
        >
          {isAutoPlay ? " Pause" : " Play"}
        </button>
      </div>

      <div className="timeline-container">
        <div className="timeline-track">
          {sections.map((section, index) => (
            <div
              key={index}
              className={`timeline-node ${activeIndex === index ? "active" : ""}`}
              onClick={() => handleSectionClick(index)}
            >
              <div className="node-year">{section.year}</div>
              <div className="node-dot"></div>
              <div className="node-label">{section.title}</div>
            </div>
          ))}
        </div>
      </div>

      <div className="timeline-content">
        <div className={`content-wrapper ${activeIndex}`}>
          {sections.map((section, index) => (
            <div
              key={index}
              className={`section-content ${activeIndex === index ? "active" : ""}`}
            >
              <div className="section-header">
                <h3 className="section-title">{section.title}</h3>
                <span className="section-year">{section.year}</span>
              </div>
              <p className="section-text">{section.content.trim()}</p>
            </div>
          ))}
        </div>
      </div>

      <div className="timeline-controls">
        <button
          className="control-btn prev-btn"
          onClick={handlePrev}
          title="Previous"
        >
           {language === "amharic" || language === "am" ? "" : "Prev"}
        </button>
        <div className="progress-indicators">
          {sections.map((_, index) => (
            <span
              key={index}
              className={`indicator ${activeIndex === index ? "active" : ""}`}
              onClick={() => handleSectionClick(index)}
            ></span>
          ))}
        </div>
        <button
          className="control-btn next-btn"
          onClick={handleNext}
          title="Next"
        >
          {language === "amharic" || language === "am" ? "" : "Next"} 
        </button>
      </div>

      <div className="timeline-info">
        <p className="info-text">
          {activeIndex + 1} of {sections.length}
        </p>
      </div>
    </div>
  );
};

export default ChurchHistoryTimeline;
